@import url(https://fonts.googleapis.com/css?family=Monoton&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);
#main {
  background-color: rgb(79, 169, 185);
  background-image: url("");
  background-repeat: no-repeat;
  background-size: cover; 
  
}


.reset {
	box-shadow: 0px 0px 0px 2px #383838;
	background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #3c136b), to(#000000));
	background:linear-gradient(to bottom, #3c136b 5%, #000000 100%);
	background-color:#3c136b;
	border-radius:10px;
	border:1px solid #000000;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Times New Roman;
	font-size:12px;
	font-weight:bold;
	padding:12px 37px;
	text-decoration:none;
  text-shadow:0px 1px 0px #000000;
  position: relative;
  top: 20px;
  left: 25%;
}
.reset:hover {
	background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #000000), to(#3c136b));
	background:linear-gradient(to bottom, #000000 5%, #3c136b 100%);
	background-color:#000000;
}
.reset:active {
	position:relative;
  top:1px;
  left: 22%;
}
#banner-text{
  text-align: center;
  position: absolute;
  top: 20%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  font-family: 'Monoton', cursive;
  font-size: 50px;

}

.nav{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  position: absolute;
  width: 100%;
  background: black;
  opacity: .8;
  color: white;
  padding: 20px 0px;
  text-transform: uppercase;
  font-family: 'Roboto Condensed', sans-serif;
}
.nav li:hover{
  
}
@media screen and (max-width: 1200px) {
  .reset {
    left: 15%;
  }
}


body {
  margin: auto;
}

.board-row {
  
  display: flex;
  flex-direction: row;
  margin-left:auto; 
  margin-right:auto;
  
}

.board-container{
  position: absolute;
  top: 25%;
  left: 50%;
  margin: auto;
  padding: 0;
}



.game-pieces {
  position: relative;
  left: -50%;
  background: transparent;
  border: 1px solid transparent;
  float: left;
  font-size: 8px;
  font-weight: bold;
  line-height: 34px;
  height: 75px;
  margin-right: -1px;
  margin-top: -1px;
  text-align: center;
  width: 75px; 
  border: solid;
  border-color: transparent;
}

.display-spaces {
  position: relative;
  left: -50%;
  background: transparent;
  border: 1px solid transparent;
  float: left;
  font-size: 8px;
  font-weight: bold;
  line-height: 34px;
  height: 75px;
  margin-right: -1px;
  margin-top: -1px;
  text-align: center;
  width: 75px;
  color: white;
  border: solid;
  border-color: lightgray;
}

.dark-space{
background-color: RGB(125, 135, 150);
}

.light-space{
    background-color: RGB(232, 235, 239);
}

@-webkit-keyframes flash {
  0% { opacity: .3; }
  50% { opacity: 1; }
 }
 @keyframes flash {
  0% { opacity: .3; }
  50% { opacity: .7; }
 }

 .flash {
  background-color: rgb(117, 243, 95);
  opacity: .3;
  -webkit-animation: flash 9s;
  animation: flash 9s;
 }

 @-webkit-keyframes flash {
  0%, 50%, 100% { opacity: .5; }
  25%, 75% { opacity: 0; }
 }
 @keyframes flash {
  0%, 50%, 100% { opacity: 0.5; }
  25%, 75% { opacity: 0; }
 }

 .dead-homies-white {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  position: relative;
  left: 15%;
  top: 240px;
  background: transparent;
  border: 1px solid transparent;
  float: left;
  font-size: 8px;
  font-weight: bold;
  color: white;
  margin: auto;
}

 .dead-homies-black {
  display: flex;
  justify-content: space-evenly;
  left: 60%;
  top: 240px;
  position: relative;
  background: transparent;
  border: 1px solid transparent;
  float: left;
  font-size: 8px;
  font-weight: bold;
  color: white; 
 }

 ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 150px;
}


ul li{
  display: flex;

}

.icon{
  width: 60px;
}

@media screen and (max-width: 900px) {
  .icon {
    width: 40px;
  }

  .display-spaces, .game-pieces {
    width: 50px;
    height: 50px;
  }

  .dead-homies-white{
    left: 0;
  float: left;
  top: 190px;
  }

  .dead-homies-black{
    left: 0;
    padding: 0;
  float: right;
  top: 190px;
  }
  
  ul{
    width: 100px;
    margin: 0;
  }
}

